@import url(https://fonts.googleapis.com/css2?family=Petit+Formal+Script&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.outer-container {
  background-color: lightblue;
  background-image: url(/mytypography/static/media/minimalistbackground.10395a32.jpg);

  display: flex;
  justify-content: center;

  height: 100vh;
}

.container {
  background-color: white;
  padding-top: 10%;

  width: 80% !important;
  margin-left: 10% !important;
  margin-right: 10% !important;

  border-left: solid 0.1px;
  border-right: solid 0.1px;
  border-color: #dedede;

  height: 100%;
}
.container-body {
  width: 94% !important;
  margin: 3% !important;

  height: calc(84% - 48px); /* -6% margin, -10% padding-top, -48px heading -22px footing */
}

.input-bar-wrapper {
  height: 35%;

  padding-left: 5px;
  padding-right: 10px;

  margin-bottom: 2%;

  font-size: 12px;
  font-weight: bold;
}

.font-result-wrapper {
  height: 65%;
  border-style: ridge double;
  border-width: 8px 6px;
  border-color: #f75348 #ed2445;
}

.zero-padding {
  padding: 0px !important; 
}

/******************* //  MEDIA QUERIES  // *******************/

/**  LANDSCAPE ORIENTATION  **/

@media screen and (orientation : landscape) {
  .container { padding-top: 5%; }
  .container-body { 
    margin: 3% !important;
    width: 94% !important;
    height: calc( 89% - 48px - 20px);
  }
  .input-bar-wrapper { 
    font-size: 11px;
    height: 40%;
  }
  .font-result-wrapper { height: 60% }
}

@media screen and (min-width: 598px) and (orientation : landscape) {
  .container-body { height: calc(89% - 48px - 20px - 10px); }
  .input-bar-wrapper { font-size: 13px; }
}

@media screen and (min-width: 900px) and (orientation : landscape) {
  .container-body { 
    margin-left: 5% !important;
    margin-right: 5% !important;
    width: 90% !important;
    height: calc(89% - 56px - 20px - 10px);
  }
  .input-bar-wrapper { font-size: 16px; }
}

@media screen and (min-width: 1280px) and (orientation : landscape) {
  .container-body { 
    height: calc(89% - 72px - 30px - 10px);
  }
  .input-bar-wrapper { font-size: 22px; }
}


/**  PORTRAIT ORIENTATION  **/

@media screen and (min-height: 568px) and (orientation : portrait) {
  .input-bar-wrapper { height: 30% }
  .font-result-wrapper { height: 70% }
}

@media screen and (min-height: 640px) and (orientation : portrait) {
  .container-body {
    margin: 5% !important;
    width: 90% !important;
  }

  .input-bar-wrapper {
    font-size: 14px;
  }
}

@media screen and (min-height: 900px) and (orientation : portrait) {
  /* start of tablet styles*/
  .container { padding-top: 13% }
  .input-bar-wrapper { 
    font-size: 18px;
    height: 30% 
  }
  .font-result-wrapper { height: 70% }
}

@media screen and (min-height: 1280px) and (orientation : portrait) {
  .input-bar-wrapper { font-size: 22px;}
}

/* bootstrap breakpoint of medium tablet styles, inputBar and FontProperties in one line */
@media screen and (min-width: 768px) {
  .input-bar-wrapper { height: 20% }
  .font-result-wrapper { height: 80% }
}
